"use client"
import React, { useEffect, useState } from 'react'
import API from '@/lib/api'
import Link from 'next/link'

export default function ProfilePage() {
  const [user, setUser] = useState<any>(null)
  const [editing, setEditing] = useState(false)
  const [name, setName] = useState('')
  const [bio, setBio] = useState('')
  const [city, setCity] = useState('')
  const [groups, setGroups] = useState<any[]>([])
  const [selectedGroup, setSelectedGroup] = useState('')
  const [quickTitle, setQuickTitle] = useState('')
  const [quickStartsAt, setQuickStartsAt] = useState('')
  const [quickCity, setQuickCity] = useState('')
  const [quickLoading, setQuickLoading] = useState(false)
  const [quickError, setQuickError] = useState<string | null>(null)

  useEffect(() => {
    const token = localStorage.getItem('token')
    if (!token) return
    API.get('/users/me', { headers: { Authorization: `Bearer ${token}` } })
      .then((r) => {
        setUser(r.data.user)
        setName(r.data.user.name || '')
        setBio(r.data.user.bio || '')
        setCity(r.data.user.city || '')
      })
      .catch(() => null)
    // fetch groups for quick event creation
    API.get('/groups')
      .then(r => setGroups(r.data.groups || []))
      .catch(() => null)
  }, [])

  const handleSave = async () => {
    const token = localStorage.getItem('token')
    if (!token) return
    try {
      const res = await API.patch('/users/me', { name, bio, city }, { headers: { Authorization: `Bearer ${token}` } })
      setUser(res.data.user)
      setEditing(false)
    } catch (err) {
      alert('Failed to update profile')
    }
  }

  const createQuickEvent = async (e: React.FormEvent) => {
    e.preventDefault()
    setQuickLoading(true)
    setQuickError(null)
    try {
      if (!selectedGroup) throw new Error('Select a group')
      const payload = { groupId: selectedGroup, title: quickTitle, startsAt: quickStartsAt, city: quickCity || city }
      const res = await API.post('/events', payload)
      window.location.href = `/events/${res.data.event.id}`
    } catch (err: any) {
      setQuickError(err?.response?.data?.error || err.message || 'Failed to create')
    } finally {
      setQuickLoading(false)
    }
  }

  if (!user) return <div className="p-6 text-white">Please login to view your profile.</div>

  return (
    <div className="mx-auto max-w-3xl p-6">
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-semibold text-white">{user.name || user.email}</h2>
        {!editing && (
          <button onClick={() => setEditing(true)} className="rounded bg-pink-500 px-4 py-2 text-sm text-white hover:bg-pink-600">
            Edit
          </button>
        )}
      </div>
      {editing ? (
        <div className="mt-6 flex flex-col gap-3">
          <input value={name} onChange={(e) => setName(e.target.value)} placeholder="Name" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white" />
          <textarea value={bio} onChange={(e) => setBio(e.target.value)} placeholder="Bio" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white" rows={3} />
          <input value={city} onChange={(e) => setCity(e.target.value)} placeholder="City" className="rounded border border-white/30 bg-white/10 px-3 py-2 text-white" />
          <div className="flex gap-2">
            <button onClick={handleSave} className="rounded bg-pink-500 px-4 py-2 text-white hover:bg-pink-600">Save</button>
            <button onClick={() => setEditing(false)} className="rounded border border-white/30 px-4 py-2 text-white hover:bg-white/10">Cancel</button>
          </div>
        </div>
      ) : (
        <div className="mt-4 text-zinc-300">
          <p className="text-sm">{user.bio || 'No bio yet'}</p>
          <p className="mt-2 text-sm text-zinc-400">{user.city || 'Location not set'}</p>
        </div>
      )}

      {/* Quick create event */}
      <div className="mt-10 rounded-2xl border border-white/10 bg-white/5 p-6 backdrop-blur-md">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-white">Quick Create Event</h3>
          <Link href="/events/create" className="text-xs text-pink-400 hover:text-pink-300">Advanced form â†’</Link>
        </div>
        <form onSubmit={createQuickEvent} className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="md:col-span-1">
            <select value={selectedGroup} onChange={(e) => setSelectedGroup(e.target.value)} className="w-full rounded-lg border border-white/20 bg-white/10 px-3 py-2 text-white">
              <option value="" className="text-black">Select group</option>
              {groups.map(g => (
                <option key={g.id} value={g.id} className="text-black">{g.name}</option>
              ))}
            </select>
          </div>
          <input value={quickTitle} onChange={(e) => setQuickTitle(e.target.value)} placeholder="Title" className="rounded-lg border border-white/20 bg-white/10 px-3 py-2 text-white md:col-span-1" required />
          <input type="datetime-local" value={quickStartsAt} onChange={(e) => setQuickStartsAt(e.target.value)} className="rounded-lg border border-white/20 bg-white/10 px-3 py-2 text-white md:col-span-1" required />
          <input value={quickCity} onChange={(e) => setQuickCity(e.target.value)} placeholder="City (optional)" className="rounded-lg border border-white/20 bg-white/10 px-3 py-2 text-white md:col-span-1" />
          <div className="md:col-span-3 flex justify-end gap-3">
            {quickError && <p className="text-xs text-red-400 flex-1">{quickError}</p>}
            <button disabled={quickLoading} className="rounded-xl bg-gradient-to-r from-pink-500 to-purple-600 px-5 py-2 text-sm font-semibold text-white disabled:opacity-50">
              {quickLoading ? 'Creating...' : 'Create'}
            </button>
          </div>
        </form>
        <p className="mt-3 text-xs text-zinc-400">Use the advanced form to add description, tags, venue, capacity and poster.</p>
      </div>
    </div>
  )
}
